import * as React from "react";
import $ from 'jquery';
import swal from "sweetalert2";
import api from "./api"

export default class IssueList extends React.Component {
    constructor(props) {
        super(props);

        let today = new Date();
        let day = today.getDay();
        let nextFriday = new Date();
        if (day <= 5) {
            nextFriday.setDate(today.getDate() + (5 - day));
        } else {
            nextFriday.setDate(today.getDate() + 5 + (day - 5));
        }
        nextFriday.setHours(17);
        nextFriday.setMinutes(0);
        nextFriday.setSeconds(0);
        let dd = String(nextFriday.getDate()).padStart(2, '0');
        let mm = String(nextFriday.getMonth() + 1).padStart(2, '0'); //January is 0!
        let yyyy = nextFriday.getFullYear();
        let nextFridayDisplay = dd + '/' + mm + '/' + yyyy;
        this.state = {
            lastestRelease: this.props.lastestRelease,
            dueDateDisplay: nextFridayDisplay,
            dueDate: nextFriday.toISOString(),
            suggestRelease: this.props.suggestRelease,
        };
    }

    componentDidUpdate(oldProps) {
        const newProps = this.props;
        if (oldProps.lastestRelease !== newProps.lastestRelease) {
            this.setState({
                lastestRelease: this.props.lastestRelease,
            })
        }
        if (oldProps.suggestRelease !== newProps.suggestRelease) {
            this.setState({
                suggestRelease: this.props.suggestRelease,
            })
        }

        if (oldProps.dueDateDisplay !== newProps.dueDateDisplay) {
            this.setState({
                dueDateDisplay: this.props.dueDateDisplay,
            })
        }

        if (oldProps.dueDate !== newProps.dueDate) {
            this.setState({
                dueDate: this.props.dueDate,
            })
        }
    }

    createMileStone = (issueList, version, projectId) => {
        let self = this;
        let dataCreate = {
            due_date: this.state.dueDate,
            title: version,
            description: version
        };
        $('.loading').show();
        api.createMilestone(projectId, dataCreate).then(function (response) {
            if (response.ok) {
                response.json().then(function (dataMilestone) {
                    api.getIssueList(projectId).then(function (response) {
                        if (response.ok) {
                            response.json().then(function (data) {
                                self.processArray(data, issueList, dataMilestone);
                            });

                        } else {
                            alert('Error ! Please try again !');
                        }
                    });
                });
            } else {
                alert('Error ! Please try again !');
            }
        });
    };

    updateIssueToMilestone = async (item, issueList, dataMilestone) => {
        if (issueList.includes(item.title.replace(/\s+/g, ' ').trim()) && item.web_url.includes('issue')) {
            await api.updateIssueToMilestone(item.iid, item.project_id, dataMilestone.id);
        }
    };

    processArray = async (array, issueList, dataMilestone) => {
        let self = this;
        for (const item of array) {
            await self.updateIssueToMilestone(item, issueList, dataMilestone);
        }
        swal.fire({
            title: '<strong>Success!</strong>',
            type: 'success',
            // eslint-disable-next-line no-useless-concat
            html: '<b>' + dataMilestone.title + ': </b>'+ "<a href='" + dataMilestone.web_url + "'>" + dataMilestone.web_url + '</a>',
            showCloseButton: true
        });
        $('.loading').hide();
    };

    handleChange = (event) => {
        this.setState({
            'suggestRelease': event.target.value
        })
    };

    handleChangeDue = (event) => {
        let dateDisplay = event.target.value;
        this.setState({
            'dueDateDisplay': dateDisplay
        });
        let date1 = dateDisplay.split('/')
        let newDate = date1[1] + '/' + date1[0] + '/' + date1[2];

        let date = new Date(newDate);

        date.setHours(17);
        date.setMinutes(0);
        date.setSeconds(0);

        this.setState({
            'dueDate': date.toISOString()
        });
    };

    render() {
        let hasFeature = '';
        if (this.props.hasFeatures) {
            hasFeature = ' - Has Features';
        }
        return (
            <React.Fragment>
                <div>
                    <div className="container issue-repos">
                        <label className={'row font-weight-bold'}>{this.props.reposLabel}</label>
                        <label className={'row'}>{'Lastest Released: ' + this.props.lastestRelease}</label>
                        <div className="row">
                            <label>{'Suggest Released'}</label>{hasFeature}
                            <input type="text" className="form-control" value={this.state.suggestRelease}
                                   onChange={this.handleChange}/>
                        </div>
                        <div className="row">
                            <label>{'Deadline'}</label>{hasFeature}
                            <input type="text" className="form-control" value={this.state.dueDateDisplay}
                                   onChange={this.handleChangeDue}/>
                        </div>
                    </div>

                    {this.props.issueList.map(
                        (value, index) => {
                            return <p key={index}>{value}</p>
                        }
                    )}
                    <div className="form-group">
                        <button className="btn btn-primary"
                                onClick={() => this.createMileStone(this.props.issueList, this.state.suggestRelease, this.props.projectId)}>Create
                            Milestone
                        </button>
                    </div>

                </div>
            </React.Fragment>
        );
    }
}


