import * as React from "react";
import ReactDOM from "react-dom";
import "react-mde/lib/styles/css/react-mde-all.css";
import $ from 'jquery';
import store from 'store';
import 'bootstrap/dist/css/bootstrap.min.css';
import 'bootstrap/dist/js/bootstrap.min';
import './App.css';
import {HashRouter as Router, Route, Link} from "react-router-dom";
import createIssue from "./page/createIssue";
import createMilestone from "./page/createMilestone";
import createRelease from "./page/createRelease";
import swal from 'sweetalert2';
import api from './api';

export default class App extends React.Component {

    constructor(props) {
        super(props);
        if (store.get('authorize-github-base64') || store.get('token')) {
            this.state = {
                login: true
            };
        } else {
            this.state = {
                login: false
            };
        }
    }

    login = async (e) => {
        let self = this;
        let loading = $('.loading');
        let token = $('#token');
        e.preventDefault();
        if (token.val()) {
            let authorizationKey = token.val();
            loading.show();
            try {
                let response = await api.checkLoginToken(authorizationKey);
                if (response.ok) {
                    store.set('token', authorizationKey);
                    self.setState({
                        login: true
                    })
                } else {
                    swal.fire({
                        type: 'error',
                        title: 'Oops...',
                        text: 'Something went wrong!'
                    });
                }
            } catch (err) {
                swal.fire({
                    type: 'error',
                    title: 'Oops...',
                    text: 'Something went wrong!'
                });
            }
            loading.hide();
        } else {
            swal.fire({
                type: 'error',
                title: 'Oops...',
                text: 'Please re-fill information!'
            });
        }
    };

    logout = async (e) => {
        e.preventDefault();
        let self = this;
        try {
            let result = await swal.fire({
                title: 'Are you sure?',
                text: "Do you really want to logout",
                type: 'warning',
                showCancelButton: true,
                confirmButtonColor: '#3085d6',
                cancelButtonColor: '#d33',
                confirmButtonText: 'Yes, Log out!'
            });

            if (result.value) {
                store.remove('authorize-github-base64');
                store.remove('token');
                self.setState({
                    login: false
                })
            }
        } catch (err) {
            alert(err);
        }
    };

    render() {
        if (this.state.login) {
            return (
                <Router>
                    <div className="loading">Loading&#8230;</div>
                    <nav className="navbar navbar-expand navbar-light bg-light">
                        <div className="collapse navbar-collapse" id="navbarNavAltMarkup">
                            <div className="navbar-nav">
                                <Link className="nav-item nav-link" to="/issue">Create Issue</Link>
                                <Link className="nav-item nav-link" to="/milestone/">Create Milestone</Link>
                                <Link className="nav-item nav-link" to="/release/">Create Release</Link>
                                <Link className="nav-item nav-link" to='#' onClick={this.logout}>Logout</Link>
                            </div>
                        </div>
                    </nav>
                    <div className="container container-fluid">
                        <aside className="col-sm-12">
                            <Route exact path="/" component={createIssue}/>
                            <Route exact path="/issue" component={createIssue}/>
                            <Route exact path="/milestone/" component={createMilestone}/>
                            <Route exact path="/release/" component={createRelease}/>
                        </aside>
                    </div>
                </Router>
            );
        } else {
            return (
                <Router>
                    <div className="loading">Loading&#8230;</div>
                    <div className="container container-fluid">
                        <aside className="col-sm-8" id="form_wrapper">
                            <div className="card">
                                <article className="card-body">
                                    <h4 className="card-title text-center mb-4 mt-1">Sign in</h4>
                                    <hr/>
                                    <form id="login_form">
                                        <div className="form-group">
                                            <div className="input-group">
                                                <input id="token" name="token" className="form-control"
                                                       placeholder="GitLab Token"
                                                       type="text"/>
                                            </div>
                                        </div>
                                        <div className="form-group">
                                            <button type="submit" className="btn btn-primary btn-block"
                                                    onClick={this.login}> Login
                                            </button>
                                        </div>
                                    </form>
                                </article>
                            </div>
                        </aside>
                    </div>
                </Router>
            );
        }
    }
}

const rootElement = document.getElementById("root");
ReactDOM.render(<App/>, rootElement);


