import * as React from "react";
import ReactMde from "react-mde";
import * as Showdown from "showdown";
import "react-mde/lib/styles/css/react-mde-all.css";
import $ from 'jquery';
import store from 'store';
import Config from "../config";
import swal from 'sweetalert2';
import api from "../api";

export default class createIssue extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            title: '',
            value: "### Preconditions (*)\n" +
                "1. \n" +
                "2. \n" +
                "\n" +
                "### Steps to reproduce (*)\n" +
                "1. \n" +
                "2. \n" +
                "\n" +
                "### Expected result (*)\n" +
                "1. \n" +
                "2. \n" +
                "\n" +
                "### Actual result (*)\n" +
                "1. \n" +
                "2. \n" +
                "\n",
            tab: "write",
            labelType: ['Type: Bug report', 'Type: Feature request', 'Type: Developer issue']
        };
        this.converter = new Showdown.Converter({
            tables: true,
            simplifiedAutoLink: true,
            strikethrough: true,
            tasklists: true
        });
        this.repository = Config.repository;
    }

    handleValueChange = value => {
        this.setState({value});
    };

    handleTabChange = tab => {
        this.setState({tab});
    };

    createIssue = async () => {
        $('.loading').show();
        if ($('#title').val() && this.state.value) {
            let call = [];
            let repos = Config.repoUrl;
            repos.map(
                (value, index) => {
                    value.message = '';
                    return value;
                }
            );
            this.repository.map((value) => {
                if ($('#' + value.value).length > 0 && $('#' + value.value).is(':checked')) {
                    call.push(
                        new Promise((resolve, reject) => {
                            api.createIssue(
                                value.projectId,
                                // eslint-disable-next-line 
                                '[' + value.release_branch + ']' + ' ' + $('#title').val(),
                                [$('#label_type').val()],
                                // eslint-disable-next-line
                                this.state.value.replace('### Preconditions (*)', '### Preconditions (*)' + '\n' + value.label + '\n')
                            ).then(response => {
                                if (response.ok) {
                                    response.json().then(obj => {
                                        repos.map(
                                            (value, index) => {
                                                if (obj.web_url.includes(value.value + '/')) {
                                                    // eslint-disable-next-line no-useless-concat
                                                    repos[index].message = repos[index].message + "<a href='" + obj.web_url + "'>" + obj.title + '</a>' + '<br/>';
                                                }
                                                return value;
                                            }
                                        );
                                        resolve(obj);
                                    }, error => {
                                        reject(error.message);
                                    });
                                } else {
                                    reject('error');
                                }
                            }, error => {
                                reject(error.message);
                            });
                        }));
                }
                return value;
            });
            await Promise.all(call).then(function (response) {
                response.map(
                    (value) => {
                        return value;
                    }
                );
                $('.loading').hide();
                let notify = '';
                repos.map(
                    (value, index) => {
                        if (value.message) {
                            // eslint-disable-next-line no-useless-concat
                            notify = notify + '<b>' + value.label + '</b> : <a href="' + 'https://gitlab.com/' + value.repos + '/issues' + '">' + 'View All Issues' + '</a>' + '<br/>' + value.message;
                        }
                        return value;
                    }
                );
                swal.fire({
                    title: '<strong>Success!</strong>',
                    type: 'success',
                    html: notify,
                    showCloseButton: true
                })
            });
        } else {
            swal.fire({
                type: 'error',
                title: 'Oops...',
                text: 'Please fill all information!'
            });
            $('.loading').hide();
        }
    };


    selectAll = (e) => {
        e.preventDefault();
        $(':checkbox').each(function () {
            this.checked = true;
        });
    };

    setBugTemplate = () => {
        this.setState({
            value: "### Preconditions (*)\n" +
                "1. \n" +
                "2. \n" +
                "\n" +
                "### Steps to reproduce (*)\n" +
                "1. \n" +
                "2. \n" +
                "\n" +
                "### Expected result (*)\n" +
                "1. \n" +
                "2. \n" +
                "\n" +
                "### Actual result (*)\n" +
                "1. \n" +
                "2. \n" +
                "\n",
            tab: "write"
        });
        $('#label_type').val('Type: Bug report');
    };

    setFeatureTemplate = () => {
        this.setState({
            value: "### Description (*)\n" +
                "\n" +
                "### Expected behavior (*)\n" +
                "\n" +
                "### Benefits\n" +
                "\n" +
                "### Additional information\n" +
                "\n",
            tab: "write"
        });
        $('#label_type').val('Type: Feature request');
    };

    setDeveloperIssueTemplate = () => {
        this.setState({
            value: "### Summary (*)\n" +
                "\n" +
                "### Examples (*)\n" +
                "\n" +
                "### Proposed solution\n" +
                "\n",
            tab: "write"
        });
        $('#label_type').val('Type: Developer issue');
    };

    render() {
        if (store.get('authorize-github-base64') || store.get('token') ) {
            return (
                <React.Fragment>
                    <div className="container">
                        <div className="form-group" id={'title_group'}>
                            <label htmlFor="title" className={'font-weight-bold'}>Title Issue</label>
                            <input type="text" className="form-control" id="title" name="title"/>
                        </div>
                        <div className={'font-weight-bold'}>Content</div>
                        <div id={'template_button'} aria-label="Basic example">
                            <button type="button" className="btn btn-primary" onClick={this.setBugTemplate}>Bug
                                Template
                            </button>
                            <button type="button" className="btn btn-primary" onClick={this.setFeatureTemplate}>Feature
                                Template
                            </button>
                            <button type="button" className="btn btn-primary"
                                    onClick={this.setDeveloperIssueTemplate}>Develop Issue Template
                            </button>
                        </div>
                        <br/>
                        <div id={'container-body'}>
                            <ReactMde
                                onChange={this.handleValueChange}
                                onTabChange={this.handleTabChange}
                                value={this.state.value}
                                generateMarkdownPreview={markdown =>
                                    Promise.resolve(this.converter.makeHtml(markdown))
                                }
                                selectedTab={this.state.tab}
                            />
                        </div>
                        <div className="form-group">
                            <label htmlFor="label_type" className={'font-weight-bold'}>Issue Type</label>
                            <select className="form-control" name={'label_type'} id={'label_type'}>
                                {this.state.labelType.map((value, index) => {
                                    return <option key={index} value={value}>{value}</option>
                                })}
                            </select>
                        </div>
                        <a onClick={this.selectAll} href={'/'}>Select All</a>
                        <div id={'repos-list'}>
                            {this.repository.map((value, index) => {
                                return (
                                    <div key={'form-check' + index} className="form-check">
                                        <input className={'form-check-input'} type="checkbox" id={value.value}
                                               name={value.value} value={value.value}/>
                                        <label className={'form-check-label'}
                                               htmlFor={value.value}>{value.label}</label>
                                    </div>
                                )
                            })}
                        </div>
                        <div id={'button-list'}>
                            <button className="btn btn-primary" id={'create_issue_button'}
                                    onClick={this.createIssue}>Create Issue
                            </button>
                        </div>

                    </div>
                </React.Fragment>
            );
        }
    }
}
