import * as React from "react";
import ReactMde from "react-mde";
import * as Showdown from "showdown";
import "react-mde/lib/styles/css/react-mde-all.css";
import $ from 'jquery';
import store from 'store';
import 'bootstrap/dist/css/bootstrap.min.css';
import Config from '../config';
import swal from 'sweetalert2';
import api from "../api";

export default class App extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            mileStones: [],
            value: '',
            tab: "write",
            log: ''
        };

        this.repository = Config.repository;
        this.repoUrl = Config.repoUrl;

        this.converter = new Showdown.Converter({
            tables: true,
            simplifiedAutoLink: true,
            strikethrough: true,
            tasklists: true
        });
    }

    componentDidMount() {
        if (store.get('authorize-github-base64') || store.get('token')) {
            try {
                this.getList();
            } catch(err) {
                alert(err);
            }
        }
    }

    getList = async () => {
        let call = [];
        let self = this;
        $('.loading').show();
        let mileStonesList = [];
        this.repoUrl.map((value) => {
            call.push(
                new Promise((resolve, reject) => {
                    api.getListMileStone(value.projectId).then(response => {
                        if (response.ok) {
                            response.json().then(obj => {
                                mileStonesList = [...mileStonesList, ...obj];
                                resolve(obj);
                            }, error => {
                                reject(error.message);
                            });
                        } else {
                            reject('error');
                        }
                    }, error => {
                        reject(error.message);
                    });
                })
            );
            return value;
        });
        await Promise.all(call).then(function () {
            self.setState({
                mileStones: mileStonesList
            });
            $('.loading').hide();
        })
    };


    handleValueChange = value => {
        this.setState({value});
    };

    handleTabChange = tab => {
        this.setState({tab});
    };

    generateReleaseNotes = () => {
        this.setState({
            value: ''
        });

        let selectedMileStones = $('#mile_stones').children("option:selected").val();
        let mileStoneTitle = $('#mile_stones').children("option:selected").html();


        let projectId = '';
        let issueList = [];
        let featureList = [];
        let developList = [];
        let self = this;
        let releaseBranch = '';

        this.repository.map((value, index) => {
            if (selectedMileStones.includes(value.repos) && mileStoneTitle.includes('v' + value.release_branch)) {
                projectId = value.projectId;
                releaseBranch = value.release_branch;
            }
            return value;
        });
        let version = $('#mile_stones').children("option:selected").html();
        version = version.slice(version.indexOf("v" + releaseBranch), version.length);
        $('.loading').show();
        api.getIssueFromMileStone(projectId, mileStoneTitle).then(function (response) {
            if (response.ok) {
                let newChangeLog = self.state.value;
                // eslint-disable-next-line no-useless-concat
                newChangeLog = newChangeLog + version + '\n' + '=============' + '\n';

                // Examine the text in the response
                response.json().then(function (data) {
                    data.map((value, index) => {
                        let title = value.title;

                        let label = value.labels;
                        let type = 'bug';
                        label.map((labelChildren, index) => {
                            if (labelChildren.toLowerCase().includes('feature')) {
                                type = 'feature';
                            }
                            if (labelChildren.toLowerCase().includes('develop')) {
                                type = 'develop';
                            }
                            return labelChildren;
                        });
                        if (type === 'feature') {
                            featureList.push('[#' + value.iid + '](' + value.web_url + '): ' + title);
                        } else if (type === 'develop') {
                            developList.push('[#' + value.iid + '](' + value.web_url + '): ' + title);
                        } else {
                            issueList.push('[#' + value.iid + '](' + value.web_url + '): ' + title);
                        }

                        return value;
                    });


                    if (featureList.length) {
                        // eslint-disable-next-line no-useless-concat
                        newChangeLog = newChangeLog + '\n' + '#### Feature Updated' + '\n';

                        featureList.map((valueIssue, index) => {
                            index++;
                            newChangeLog = newChangeLog + index + '. ' + valueIssue + '\n';
                            return valueIssue;
                        });
                    }


                    if (issueList.length) {
                        // eslint-disable-next-line no-useless-concat
                        newChangeLog = newChangeLog + '\n' + '#### Bug Fixed' + '\n';

                        issueList.map((valueIssue, index) => {
                            index++;
                            newChangeLog = newChangeLog + index + '. ' + valueIssue + '\n';
                            return valueIssue;
                        });
                    }

                    if (developList.length) {
                        // eslint-disable-next-line no-useless-concat
                        newChangeLog = newChangeLog + '\n' + '#### Developer Issue' + '\n';

                        developList.map((valueIssue, index) => {
                            index++;
                            newChangeLog = newChangeLog + index + '. ' + valueIssue + '\n';
                            return valueIssue;
                        });
                    }

                    self.setState({
                        value: newChangeLog
                    });
                    $('.loading').hide();

                });
            } else {
                swal.fire({
                    type: 'error',
                    title: 'Oops...',
                    text: 'Something went wrong!'
                });
                $('.loading').hide();
            }
        });
    };

    release = async () => {
        let confirmFunction = await swal.fire({
            title: 'Are you sure?',
            text: "Do you really want to release?",
            type: 'warning',
            showCancelButton: true,
            confirmButtonColor: '#3085d6',
            cancelButtonColor: '#d33',
            confirmButtonText: 'Yes, Release!'
        });

        if (confirmFunction.value) {
            $('.loading').show();
            let selectedMileStones = $('#mile_stones').children("option:selected").val();
            let mileStoneTitle = $('#mile_stones').children("option:selected").html();
            let mileStoneId = $('#mile_stones').children("option:selected").attr('milestoneId');
            let version = $('#mile_stones').children("option:selected").html();

            let projectId = '';
            let self = this;
            let branch = '';
            let tagName = '';
            this.repository.map((value, index) => {
                if (selectedMileStones.includes(value.repos) && version.includes('v' + value.release_branch)) {
                    projectId = value.projectId;
                    branch = value.release_branch;
                    tagName = $('#mile_stones').children("option:selected").html();
                }
                return value;
            });

            let result = await api.getIssueFromMileStone(projectId, mileStoneTitle);

            if (result.ok) {
                result.json().then(async function (data) {
                    await self.closeIssueList(data);

                    let closeMileStone = api.closeMileStone(projectId, mileStoneId);

                    tagName = tagName.slice(tagName.indexOf("v" + branch), tagName.length);
                    // eslint-disable-next-line
                    let releaseMileStone = api.release(projectId, tagName, branch, version, self.state.value, [mileStoneTitle]);

                    Promise.all([closeMileStone, releaseMileStone])
                        .then(function(res){
                            let response = res[1];
                            if (response.ok) {
                                // Examine the text in the response
                                response.json().then(function (data) {
                                    swal.fire({
                                        title: '<strong>Released!</strong>',
                                        type: 'success',
                                        // eslint-disable-next-line no-useless-concat
                                        html: '<b>Release ' + data.name + ': </b>' + "<a href='" + data._links.self + "'>" + data._links.self + '</a>',
                                        showCloseButton: true
                                    });
                                    $('.loading').hide();
                                });
                            } else {
                                swal.fire({
                                    type: 'error',
                                    title: 'Oops...',
                                    text: 'Something went wrong!'
                                });
                                $('.loading').hide();
                            }
                        });
                });
            } else {
                swal.fire({
                    type: 'error',
                    title: 'Oops...',
                    text: 'Something went wrong!'
                });
                $('.loading').hide();
            }
        }
    };

    closeIssueList = async (data) => {
        let self = this;
        for (const value of data) {
            await self.closeIssue(value);
        }
    };

    closeIssue = async (value) => {
        await api.closeIssue(value.project_id, value.iid);
    };

    render() {
        if (store.get('authorize-github-base64') || store.get('token')) {
            return (
                <React.Fragment>
                    <div className="container" id={'select_mile_stones'}>
                        <div className="form-group">
                            <label htmlFor="mile_stones" className={'font-weight-bold'}>Milestones</label>
                            <select className="form-control" name={'mile_stones'} id={'mile_stones'}
                                    onChange={this.generateReleaseNotes}>
                                <option value={'0'}>--- Please Select MileStones ---</option>
                                {this.state.mileStones.map((value, index) => {
                                    return <option key={value.id} milestoneid={value.id}
                                                   value={value.web_url}>{value.title}</option>
                                })}
                            </select>
                        </div>
                    </div>
                    <div className="container">
                        <ReactMde
                            onChange={this.handleValueChange}
                            onTabChange={this.handleTabChange}
                            value={this.state.value}
                            generateMarkdownPreview={markdown =>
                                Promise.resolve(this.converter.makeHtml(markdown))
                            }
                            selectedTab={this.state.tab}
                        />
                    </div>
                    <br/>
                    <div className={'container'}>
                        <button className="btn btn-primary" onClick={this.release}>RELEASE</button>
                        <br/>
                        <br/>
                    </div>
                </React.Fragment>
            );
        }
    }
}
