import * as React from "react";
import IssueList from "../IssueList";
import store from "store";
import $ from "jquery";
import Config from "../config";
import swal from 'sweetalert2';
import api from "../api";

export default class createMilestone extends React.Component {

    constructor(props) {
        super(props);
        this.state = this.getInitialState();
    }

    getInitialState = () => {
        this.repository = Config.repository;
        this.repoUrl = Config.repoUrl;
        return {
            mileStones: [],
            value: '',
            tab: "write",
            log: '',
            nextRelease: []
        };
    };

    getList = async (releaseOrderRepos) => {
        let call = [];
        let self = this;
        let result;
        this.repoUrl.map((value) => {
            call.push(api.getListMileStoneClosed(value));
            return value;
        });
        result = await Promise.all(call);
        result.map((value, index) => {
            if (value.ok) {
                // Examine the text in the response
                value.json().then(function (data) {
                    data = data.splice(0, 3);
                    data.map(
                        (value) => {
                            self.repository.map(
                                (repo, index) => {
                                    if (value.web_url.includes(repo.repos + '/') && value.title.includes('v' + repo.release_branch) && !releaseOrderRepos[index].lastestVersion) {
                                        releaseOrderRepos[index].lastestVersion = value.title;
                                        releaseOrderRepos[index].suggestRelease = self.suggestRelease(value.title, 'v' + repo.release_branch, repo.hasFeatures);
                                    }
                                    return value;
                                }
                            );
                            return value;
                        }
                    );
                    self.setState({
                        nextRelease: releaseOrderRepos
                    });
                    $('.loading').hide();
                });
            } else {
                swal.fire({
                    type: 'error',
                    title: 'Oops...',
                    text: 'Something went wrong!'
                });
                $('.loading').hide();
            }
            return value;
        });
    };

    suggestRelease = (lastestRelease, branchVersion, hasFeature = false) => {
        let version = lastestRelease.slice(lastestRelease.indexOf(branchVersion), lastestRelease.length);
        let analyzeVersion = version.split('.');

        if (!hasFeature) {
            analyzeVersion[2] = parseInt(analyzeVersion[2]) + 1;
        } else {
            analyzeVersion[1] = parseInt(analyzeVersion[1]) + 1;
            analyzeVersion[2] = 0;
        }

        return lastestRelease.slice(0, lastestRelease.indexOf(branchVersion)) + analyzeVersion.join('.');
    };


    readSprint = async () => {
        this.setState(this.getInitialState());
        let planId = $('#plan_id').val();
        planId = planId.slice(planId.indexOf('d/'), planId.length);
        let key = Config.key;
        planId = planId.replace('d/', '');
        if (planId.indexOf('/') > -1) {
            planId = planId.slice(0, planId.indexOf('/'));
        }
        let self = this;
        $('.loading').show();
        if (store.get('authorize-github-base64') || store.get('token')) {
            let releaseOrderRepos = this.repository;
            releaseOrderRepos.map(
                (value, index) => {
                    value.issueList = [];
                    return value;
                }
            );
            try {
                let response = await api.getDataFromGoogleSheet(planId, key);
                let indexOfFeature = null;
                if (response.ok) {
                    response.json().then(function (data) {
                        let releaseVersion = 0;
                        data.values.map(
                            (value, index) => {
                                if (releaseVersion < Config.repository.length) {
                                    if (typeof value[2] !== 'undefined' && value[2].toLowerCase() !== 'release') {
                                        releaseOrderRepos[releaseVersion].issueList.push(
                                            value[2].replace(/\s+/g, ' ').trim()
                                        );
                                    }
                                    if (typeof value[1] !== 'undefined' && value[1].toLowerCase().includes('feature')) {
                                        if (!indexOfFeature) {
                                            indexOfFeature = index;
                                        }
                                    }
                                    /* Next Row After Release Line*/
                                    if (index === (indexOfFeature + 1) && value[2]) {
                                        releaseOrderRepos[releaseVersion].hasFeatures = 1;
                                        indexOfFeature = null;
                                    }
                                    if (typeof value[2] !== 'undefined' && value[2].toLowerCase() === 'release') {
                                        releaseVersion++;
                                    }
                                }
                                return value;
                            }
                        );
                        self.getList(releaseOrderRepos);
                    });
                } else {
                    swal.fire({
                        type: 'error',
                        title: 'Oops...',
                        text: 'Something went wrong!'
                    });
                    $('.loading').hide();
                }
            } catch(err) {
                alert(err); // TypeError: failed to fetch
            }
        }
    };


    render() {
        return (
            <React.Fragment>
                <div className={'container'}>
                    <div className="form-group">
                        <label htmlFor="plan_id" className={'font-weight-bold'}>Url của file Planning (Cần để public để
                            có thể đọc))</label>
                        <input type="text" className="form-control" id="plan_id" name="plan_id"/>
                    </div>

                    <button className="btn btn-primary" onClick={this.readSprint}>Read Sprint Planning</button>
                    <div className="form-group" id={'list-issue'}>
                        {this.state.nextRelease.map(
                            (value, index) => {
                                return <IssueList key={index} issueList={value.issueList} reposLabel={value.label}
                                                  suggestRelease={value.suggestRelease}
                                                  lastestRelease={value.lastestVersion}
                                                  hasFeatures={value.hasFeatures}
                                                  reposId={value.value}
                                                  projectId={value.projectId}/>;
                            }
                        )}
                    </div>
                </div>
            </React.Fragment>
        );
    }
}


